.wrapper {
  background-image: url("../images/person-bg.svg");
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: bottom;
  background-position-x: -25vw;
  background-repeat: no-repeat;
}

.container {
  padding: 40px;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 80px);
}

.content {
  margin: auto;
}

.link {
  color: #8585ad;
}
